.WaterfallConfig {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.WaterfallConfig > * {
  margin-bottom: 1rem;
}

.sideBar {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 1rem;
  max-width: 250px !important;
  -webkit-flex: 0 0 250px !important;
          flex: 0 0 250px !important;
}
.sideBar > * {
  margin-bottom: 0.5rem;
}

.dashboard {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

